import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';

import { DomSanitizer } from '@angular/platform-browser';

import { AlertController } from 'ionic-angular';


/**
 * Generated class for the ReprogramacionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reprogramacion',
  templateUrl: 'reprogramacion.html',
})
export class ReprogramacionPage {

motivoReprograma:any='';
  constructor(public navCtrl: NavController, private alertCtrl: AlertController,public navParams: NavParams,public loadingCtrl:LoadingController,private DomSanitizer: DomSanitizer,public apiProvider: RestProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReprogramacionPage');
  }

  reprogramar(){



  var datE ={estado:2,
              idCita : this.navParams.get('idCita'),
              comentario:this.motivoReprograma};
              console.log(datE);

           let loading = this.loadingCtrl.create({content : "Reprogramando...",   cssClass: "my-loading"});
   loading.present();

        this.apiProvider.cambiarServicioCitaNCREPRO(datE)
      .then(data => {

      loading.dismiss();
       console.log(data);
              if(data){
                this.navCtrl.pop();
       }
       else{
       this.reintentarAlert(this.reprogramar.bind(this));
       console.log('Ha ocurrido un error');
       }
       
       
      });




}


    reintentarAlert(funcionEnviar){

    var mensaje = `<div>  
                      <p>No hemos podido conectar. 
                      Verifica tu conexión a Internet para continuar</p>
                      
                   <div>`;
    let alert = this.alertCtrl.create({
      title: 'Error de conexión',
      subTitle: this.DomSanitizer.bypassSecurityTrustHtml(mensaje),
       buttons: [

           {
        text: 'Reintentar',
        handler: () => {
          funcionEnviar();
        }
      },
    ],
      enableBackdropDismiss: false
    });
    alert.present();

  }




}



